import React from 'react';
import { Card, CardContent, Typography, Box } from '@mui/material';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';

// Dummy image URL
const dummyImage = "data:image/jpeg;base64,"; 


<img src='./artistImage.png'/>

const SongCard = ({ song, onSelect, isCurrent }) => {
  return (
    <Card
      onClick={() => onSelect(song)}
      sx={{
        marginBottom: 1,
        cursor: 'pointer',
        transition: '0.3s',
        backgroundColor: isCurrent ? 'rgb(220,20,60)' : 'rgb(40,40,40)',
        color: 'white',
        '&:hover': {
          boxShadow: 3,
          backgroundColor: isCurrent ? 'rgb(240,30,70)' : 'rgb(60,60,60)',
        },
        borderRadius: '8px',
        display: 'flex',
        alignItems: 'center',
        padding: '8px',
      }}
    >
      <Box
        component="img"
        src={dummyImage}
        alt={song.title}
        sx={{
          width: 50,
          height: 50,
          borderRadius: '8px',
          marginRight: 2,
        }}
      />
      <CardContent sx={{ flexGrow: 1 }}>
        <Typography variant="h6">{song.title}</Typography>
        <Typography variant="subtitle2" sx={{ opacity: 0.8 }}>
          {song.artist}
        </Typography>
      </CardContent>
      {isCurrent && <PlayArrowIcon sx={{ color: 'white', marginRight: 1 }} />}
    </Card>
  );
};

export default SongCard;
